import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram, getToday, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const CetakLaporanDetailBarang = (
  data = "",
  kode_baki = "",
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  // let footRows = [];
  let tableColumn = [];
  let finalY = 30;
  let total_berat = 0;
  let total_stock = 0;
  let total_berat_atribut = 0;


  doc.setFontSize(15);
  doc.text("LAPORAN BARANG DETAIL", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN BARANG DETAIL",
  });
  // doc.text(`KODE BAKI : ${kode_baki}`, 14, 25);
  tableColumn = [
    [
      {
        content: `NO`,
      },
      // {
      //   content: `KODE BARANG`,
      // },
      {
        content: `KODE BARCODE`,
      },
      {
        content: `KODE GROUP`,
      },
      {
        content: `KODE JENIS`,
      },
      {
        content: `KODE INTERN`,
      },
      {
        content: `KODE GUDANG`,
      },
      {
        content: `NAMA BARANG`,
      },
      {
        content: `BERAT`,
        styles: {
          halign: 'right'
        },
      },
      {
        content: `NAMA ATRIBUTE`,
      },
      {
        content: `BERAT ATRIBUTE`,
        styles: {
          halign: 'right'
        },
      },
      // {
      //   content: `JUMLAH`,
      // },
    ],
  ];

  data.forEach((item, index) => {
    // item.list_barang.forEach((barang, index) => {
    let rows = [
      // ++index,
      {
        content: `${++index}`,
        styles: {
          cellWidth : 10
        },
      },
      // item.kode_barang,
      item.kode_barcode,
      item.kode_group,
      item.kode_dept,
      item.kode_intern,
      item.kode_gudang,
      item.nama_barang,
      {
        content: formatGram(item.berat,3),
        styles: {
          halign: 'right'
        },
      },
      item.nama_atribut,
     
      {
        content: formatGram(item.berat_atribut,3),
        styles: {
          halign: 'right'
        },
      }
      // item.stock_on_hand,
     
    ];
    total_berat = total_berat + item.berat;
    total_stock = total_stock + item.stock_on_hand;
    total_berat_atribut = total_berat_atribut + item.berat_atribut;
    tableRows.push(rows);
  });
 
  let footer = [
    {
      content: `Total`,
      colSpan: 7,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${formatGram(total_berat,3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: 'right'
      },
    },
    {
      content: ``,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${formatGram(total_berat_atribut,3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: 'right'
      },
    },

    // `${sub_qty}`,
  ];
  tableRows.push(footer);
  let printed = [
    {
      colSpan: 6,
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    // foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    bodyStyles: {
      // lineWidth: 0.01,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      textColor: "#000",
      fillColor: "#E8E5E5",
    },
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.save(`LAPORAN BARANG DETAIL.pdf`);
 
};

export default CetakLaporanDetailBarang;
